import { FC } from 'react';
import {
  DefaultProps,
  Grid as MantineGrid,
  GridProps as MantineGridProps,
  ColProps as MantineColProps,
} from '@mantine/core';
import { Size, TwBaseProps } from '../../types';
import { styled } from '../../utils';
import { PropsFrom } from '../../utility-types';

export interface GridProps extends TwBaseProps, Omit<MantineGridProps, keyof DefaultProps> {
  gutter?: Size;
  gutterLg?: Size;
  gutterMd?: Size;
  gutterSm?: Size;
  gutterXl?: Size;
  gutterXs?: Size;
}

// TODO: See what to omit from here
interface IGridCol extends MantineColProps {}

interface IGrid extends FC<GridProps> {
  Col: FC<PropsFrom<typeof MantineGrid.Col> & IGridCol>;
}

const Root = styled(MantineGrid)<MantineGridProps>(() => ({}));

export const Grid: IGrid = (props) => {
  return <Root {...props} />;
};

Grid.Col = (props: IGridCol) => {
  return <MantineGrid.Col {...props} />;
};
